* {
  box-sizing : border-box
}

body {
  margin: 0;
}

.App {
  display: flex;
  flex-direction: column;
}

.nav {
  background-color: white;
  color: #333;
  display: flex;
  justify-content: center;
  grid-gap: 2rem;
  gap: 2rem;
  padding: 0 1rem;
}

.nav ul {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
}

.nav a {
  color: inherit;
  text-decoration: none;
  height: 100%;
  display: flex;
  align-items: stretch;
  padding: .25rem;
}

.nav li.active {
  text-decoration: underline;
  text-underline-offset: 5px;
}

.nav li:hover{
  text-decoration: underline;
  text-underline-offset: 5px;
}
.App {
  align-items: center;
  text-align: center;
  margin-top: 25px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.content {
  display: flex;
  justify-content: center;
  width: 80%;
}
.home {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-top: 10px;
    height: 80%;
    width: 100%;
}

.home > div {
    flex: 1 1;
}

.home div img {
    width: 100%;
    height: 100%;
}

.info-item {
    background-color: #F9F2EC;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.info-item > h1 {
    font-size: 300%;
}

.info-item > h2 {
    font-size: 200%;
}

.info-item > .btn-custom {
    color: white;
    background-color: #A4865D;
    width: 50%;
    height: 10%;
    letter-spacing: 5px;
}
.about {
    display: flex;
    flex-direction: column;
    width: 60%;
}

.our-team {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.our-team > p {
    font-size: large;
    text-align: center;
    inline-size: 50%;
    padding: 25px;
    width: 100%;
}

.about-us {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.about-us > img {
    width: 100%;
}

.about-us > div {
    display: flex;
    flex-direction: column;
    background-color: #FBF2EB;
    align-items: center;
    width: 100%;
}

.about-us > div > p {
    color: #858381;
    font-size: 20px;
    text-align: center;
    padding: 10px;
}

.about-us > div > button {
    color: white;
    background-color: #A4865D;
    font-size: x-large;
    width: 50%;
    height: 50%;
    letter-spacing: 5px;
    margin-bottom: 20px;
}

.bottom {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-top: 50px;
}

.bottom > img {
    width: 45%;
}
.contact {
    display: flex;
    flex-direction: column;
    width: 100%;
}

Form {
    width: 50%;
    margin: 25px 0px;
}


.btn-custom {
    color: white;
    background-color: #A4865D;
}
